<template>
  <v-app-bar color="grey-lighten-2">
    <v-btn icon @click="fetchData">
      <v-icon>mdi-dots-vertical</v-icon>
    </v-btn>
    <div>{{ response }}</div>
  </v-app-bar>
</template>

<script>
export default {
  components: {},
  name: "app-bar",
  props: {},
  data: () => ({
    response: {},
  }),
  watch: {},
  created() {},
  mounted() {},
  methods: {
    fetchData: function () {
      this.$axios
        .get("https://jsonplaceholder.typicode.com/users/")
        .then((response) => {
          this.response = response;
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>

<style scoped></style>
